<template>
  <div class="ServiceAdminEdit">
    <h2>{{handleType=="report"?"举报信息":"反馈信息 "}}</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">{{handleType=="report"?"举报时间：":"反馈时间："}}</div>
        <el-input v-model="form.name" disabled></el-input>
      </el-col>
    </el-row>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">{{handleType=="report"?"举报人优号：":"反馈人优号："}}</div>
        <el-input v-model="form.hometownCity" disabled></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">{{handleType=="report"?"举报人昵称：":"反馈人昵称："}}</div>
        <el-input v-model="form.schoolName" disabled></el-input>
      </el-col>
    </el-row>
    <el-row :gutter="50" v-if="handleType=='report'">
      <el-col :span="8">
        <div class="name">被举报人优号：</div>
        <el-input v-model="form.autograph" disabled></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">被举报人昵称：</div>
        <el-input v-model="form.autograph" disabled></el-input>
      </el-col>
    </el-row>
    <h2>{{handleType=="report"?"举报理由资料":"反馈资料"}}</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8" v-if="handleType=='report'">
        <div class="name">举报理由：</div>
        <el-input v-model="form.name" disabled></el-input>
      </el-col>
    </el-row>
    <el-row :gutter="50">
      <el-col :span="16">
        <div class="name">{{handleType=="report"?"举报详情：":"反馈意见："}}</div>
        <el-input type="textarea" v-model="form.name" disabled></el-input>
      </el-col>
    </el-row>
    <div class="img">
      <div class="name">{{handleType=="report"?"举报图片：":"反馈图片："}}</div>
      <div class="img-item" v-for="(item, index) in srcList" :key="index">
        <el-image style="width:120px" lazy="true" :preview-src-list="srcList" :src="item"></el-image>
      </div>
    </div>
    <h2>{{handleType=="report"?"处理意见：":"反馈理由："}}</h2>
    <el-divider></el-divider>

    <el-row :gutter="50" v-if="handleType=='report'">
      <el-col :span="24">
        <div class="name">处理意见：</div>
        <div>
          <el-radio v-model="form.name" label="1" border size="medium">禁止登录</el-radio>
          <el-radio v-model="form.name" label="2" border size="medium">下架禁播</el-radio>
          <el-radio v-model="form.name" label="3" border size="medium">提示警告</el-radio>
        </div>
      </el-col>
    </el-row>

    <el-row :gutter="50">
      <el-col :span="16">
        <div class="name">{{handleType=="report"?"处理理由：":"反馈理由："}}</div>
        <el-input type="textarea" v-model="form.name"></el-input>
      </el-col>
    </el-row>

    <el-row :gutter="50">
      <el-col :span="8">
        <el-button @click="close">关闭</el-button>
        <el-button @click="preservation">保存</el-button>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //处理类型 ， 用户判断是举报还是反馈
      handleType: this.$route.query.type,
      srcList: [
        "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1416536552,3950553019&fm=26&gp=0.jpg",
        "https://dss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3521319392,1160740190&fm=26&gp=0.jpg",
        "https://dss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1068485212,3292662520&fm=111&gp=0.jpg"
      ],
      form: {
        name: "王大拿", //昵称
        sex: 1, //性别
        hometownCity: "", //家乡城市
        schoolName: "", //学校
        birthday: "", //出生日期
        autograph: "" //个性签名
      }
    };
  },
  methods: {
    // 保存
    preservation() {
      this.$confirm("是否保存？", {
        confirmButtonText: "保存",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$router.go(-1);
        })
        .catch(() => {});
    },
    //关闭
    close() {
      this.$confirm("是否关闭？", {
        confirmButtonText: "关闭",
        cancelButtonText: "取消"
      }).then(() => {
        this.$router.go(-1);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.ServiceAdminEdit {
  padding-bottom: 200px;
}
h2 {
  padding-top: 30px;
}
.img {
  display: flex;
  font-size: 12px;
  align-items: center;
  margin: 20px 0;
  .img-item {
    margin-right: 20px;
    border: 2px solid #ccc;
  }
}
</style>